.container {
  margin-left: 1rem;
  margin-right: 1rem;
  display: flex;
  flex-direction: column;
}

.pageCenter {
  width: auto;
  height: 30vh;
  align-items: center;
  justify-content: center;
  padding-bottom: 30vh;
}

.fullWidth {
  margin: 0;
  background-color: white;
}

.light {
  background-color: rgba(255, 255, 255, 0.8);
}

@media only screen and (min-width: 768px) {
  .pageCenter {
    height: 90vh;
    padding-bottom: 0;
  }
}
