h1 {
  font-weight: 500;
  font-size: 1.8rem;
  margin-top: 0.4rem;
  margin-bottom: 0.2rem;
}

h2 {
  font-weight: 500;
  font-size: 1.4rem;
  margin-top: 0.2rem;
  margin-bottom: 0.2rem;
}

p {
  margin-top: 0.2rem;
  margin-bottom: 0.2rem;
  line-height: 1.8rem;
}

p,
h1,
h2,
h3,
h4,
h5 {
  color: #333;
}

a {
  text-decoration: none;
  color: #5f27cd;
}

a:hover,
a:active {
  color: #341f97;
}

.text-center {
  text-align: center;
}

@media only screen and (min-width: 768px) {
  h1 {
    font-size: 3rem;
  }
  h2 {
    font-size: 2.5rem;
  }
}
